/* clearfix */
.container,
.row,
.col,
[class*="col-"] {
  *zoom: 1;
}
.container:before,
.container:after,
.row:before,
.row:after,
.col:before,
.col:after,
[class*="col-"]:before,
[class*="col-"]:after {
  content: "";
  width: 0;
  height: 0;
  /*display:table 可避免父子 margin 合并， block不行*/
  display: table;
  clear: both;
}
.form {
  display: table;
}
.form > * {
  display: table-row;
  padding: 0;
  line-height: 2.5;
}
.form > **:before,
.form > **:after {
  display: none;
}
.form > * > * {
  display: table-cell;
  float: none;
  padding-left: 1em;
  padding-bottom: 1em;
}
.form > * > **:before,
.form > * > **:after {
  display: none;
}
.form > * > *:first-child {
  width: 8%;
  padding-left: 2em;
  white-space: nowrap;
  text-align: right;
  color: #888;
}
@media (max-width: 600px) {
  .form,
  .form > *,
  .form > * > * {
    display: block;
    padding-left: 0 !important;
    margin: 0;
  }
  .form > * > *:first-child {
    width: auto;
    text-align: left;
    padding-bottom: 0 !important;
  }
  .form input,
  .form .input,
  .form textarea,
  .form select,
  .form .input-group {
    width: 100%;
  }
}
.required:before {
  content: '*';
  display: inline-block !important;
  margin: 0 0.5ex;
  color: #f00;
}
input,
.input,
textarea,
select,
button,
.button,
.input-group > i,
.input-group > span,
.button-group > span {
  display: inline-block;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  max-width: 100%;
  height: 30px;
  min-width: 30px;
  min-height: 30px;
  line-height: 28px;
  padding: 0 0.5em;
  border: solid 1px #ddd;
  box-shadow: 0px 0px 0px 0px #ddd;
  border-radius: 3px;
  vertical-align: middle;
  background: #fff;
  transition: 0.3s border, box-shadow;
}
input:hover,
.input:hover,
textarea:hover,
select:hover,
button:hover,
.button:hover {
  border-color: #aaa;
  box-shadow: 0px 0px 5px #ddd;
  z-index: 1;
}
input:focus,
.input:focus,
textarea:focus,
select:focus,
button:focus,
.button:focus {
  border-color: #aaa;
  box-shadow: 0px 0px 5px #ddd;
  box-shadow: 0px 0px 4px 1px #ddd;
  z-index: 2;
}
input[disabled],
.input[disabled],
textarea[disabled],
select[disabled],
button[disabled],
.button[disabled] {
  background: #eee;
  cursor: default;
}
input,
.input,
textarea,
select,
.input-group {
  width: 250px;
}
select[multiple],
textarea {
  height: 60px;
  resize: vertical;
}
textarea[rows] {
  height: auto;
}
button,
.button {
  padding-left: 1em;
  padding-right: 1em;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
button:active,
.button:active {
  box-shadow: 0px 0px 5px #ddd inset;
  z-index: 3;
}
.button {
  overflow: hidden;
  position: relative;
}
.button input[type="file"] {
  font-size: 99em;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  cursor: pointer;
}
.input-group,
.button-group {
  display: inline-flex;
  max-width: 100%;
}
.input-group > *,
.button-group > * {
  position: relative;
  margin: 0;
  margin-right: -1px;
}
.input-group > *:not(:first-child),
.button-group > *:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.input-group > *:not(:last-child),
.button-group > *:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.input-group > i,
.button-group > i,
.input-group span,
.button-group span {
  text-align: center;
}
.input-group > input,
.button-group > input,
.input-group .input,
.button-group .input,
.input-group textarea,
.button-group textarea {
  width: 110px;
  flex: 100 100 auto;
}
.input-group > select,
.button-group > select {
  width: auto;
  flex: 10 0 auto;
}
.input-group button,
.button-group button,
.input-group .button,
.button-group .button {
  flex: 1 0 auto;
}
