select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding-right: 1.5em;
  background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="14" height="4"><path d="m 0 0, l 2.5 2.5, l 2.5 -2.5" style="fill:transparent;stroke:#aaa;stroke-width:1.125px" /></svg>');
  background-position: 100% 50%;
  background-repeat: no-repeat;
}
._options {
  position: fixed;
  max-height: 100px;
  max-height: 20em;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  top: 0;
  left: 0;
  z-index: 9999;
  padding: 0;
  margin: 0;
  margin-top: 2px;
  list-style: none;
  background: #fff;
  border: solid 1px #ddd;
  border-radius: 3px;
  box-shadow: 0px 1px 10px 1px #ddd;
}
._options ._optgroup {
  padding-left: 1em;
  margin: 0;
}
._options ._option {
  padding: 0 2em;
  line-height: 2;
  white-space: nowrap;
  cursor: pointer;
  transition: 0.3s;
}
._options ._option:hover {
  background: #eee;
}
._options ._option.selected {
  background: #0af;
  color: #fff;
}
._options ._option.disabled {
  color: #bbb;
}
._options ._option.disabled.selected {
  background: #e6f7ff;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
  text-align: left;
}
.table {
  display: table;
  width: 100%;
}
.tblf {
  table-layout: fixed;
}
.tr {
  display: table-row;
}
.table > *:before,
.table > *:after {
  display: none !important;
}
th {
  text-align: inherit;
}
.th {
  display: table-cell;
}
.table th {
  white-space: nowrap;
}
.table tr:first-child th {
  border-bottom: solid 1px #ddd;
}
.td {
  display: table-cell;
  float: none;
  padding: 0.25em 1em;
}
.table td {
  border-bottom: solid 1px #fafafa;
  transition: 0.3s;
}
.table th,
.table td,
.th,
.td {
  height: 3em;
  padding: 0.25em 1em;
  min-width: 4em;
}
.table tr:hover td {
  background: #f7f7f7;
}
