}

.ra6 {
  border-radius: 1.5em
}

.ra9 {
  border-radius: 99em
}

/* shadow */

.sh {
  box-shadow: 0 1px 5px #E2E2E2
}

/*.shr>.ova 右边白阴影提示横向滚动*/

.shr {
  position: relative;
}

.shr:after {
  content: "";
  display: block;
  position: absolute;
  top: 1px;
  right: 0;
  bottom: 1px;
  width: 1.25em;
  background: rgba(255, 255, 255, 0.9);
  background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 1));
  /*box-shadow: -0.25em 0px 1em 0.75em rgb(255, 255, 255);*/
  pointer-events: none;
  z-index: 30;
  transition: 1s;
}

.shr:hover:after {
  opacity: 0;
}

/* postion */

.rel {
  position: relative
}

.abs {
  position: absolute
}

.l0 {
  left: 0
}

.r0 {
  right: 0
}

.t0 {
  top: 0
}

.b0 {
  bottom: 0
}

.tblr0 {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0
}

/* display */

.none,
.dn {
  display: none !important
}

.inline,
.di {
  display: inline !important
}

.block,
.dib {
  display: block !important
}

.inline-block,
.inline.block,
.inbl,
.dib {
  display: inline-block;
}

.inline-table,
.inline.table,
.inta,
.intb,
.ditb {
  display: inline-table
}

/* visibility */

.vv {
  visibility: visible
}

.vh {
  visibility: hidden
}

/* hover */

/* .hv>.hvs */

.hvs {
  visibility: hidden
}

.hv:hover .hvs {
  visibility: visible
}

/* float */

.fl,
.fll {
  float: left
}

.fr,
.flr {
  float: right
}

.fn,
.fln {
  flat: none
}

.cl,
.clb {
  clear: both
}

.cll {
  clear: left
}

.clr {
  clear: right
}

.clf {
  clear: both;
  *zoom: 1
}

.clf:before,
.clf:after {
  content: "";
  display: table;
  clear: both
}

/* text */

.tac {
  text-align: center
}

.tal {
  text-align: left
}

.tar {
  text-align: right
}

.whsnw,
.wsnw,
.wsn,
.nowr {
  white-space: nowrap
}

.tove,
.toe,
.ellps {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis
}

.bold,
.fwb {
  font-weight: bold
}

.usn {
  user-select: none
}

.ti1 {
  text-indent: 1em
}

.ti2 {
  text-indent: 2em
}

.ti3 {
  text-indent: 3em
}

.ti4 {
  text-indent: 4em
}

/* vertical-align */

.vabl {
  vertical-align: baseline
}

.vam {
  vertical-align: middle
}

.vat {
  vertical-align: top
}

.vab {
  vertical-align: bottom
}

.vamb:before {
  content: "";
  display: inline-block;
  height: 100%;
  margin-right: -1ex;
  vertical-align: middle;
}

.vama:after {
  content: "";
  display: inline-block;
  height: 100%;
  margin-left: -1ex;
  vertical-align: middle;
}

.vamb>*,
.vama>* {
  display: inline-block;
  vertical-align: middle;
}

/* overflow */

.ova {
  overflow: auto
}

.ovxa {
  overflow-x: auto
}

.ovya {
  overflow-y: auto
}

.ovh {
  overflow: hidden
}

.ovxh {
  overflow-x: hidden
}

.ovyh {
  overflow-y: hidden
}

/* cursor */

.curp {
  cursor: pointer
}

.curd {
  cursor: default
}

.curhe,
.curhp {
  cursor: help
}

/* others */

.apn {
  -webkit-appearance: none
}

.pen,
.pn {
  pointer-events: none
}